import React, { useState } from 'react';
import './EditarNegocio.css';

const EditarNegocio = () => {
  const [negocios, setNegocios] = useState([
    {
      id: 1,
      nombreNegocio: "Tienda Lazo",
      propietario: "Jose Lazo",
      email: "tiendalazo@gmail.com",
      telefono: "996923731",
      direccion: "Av. Principal 123",
      estado: "Activo",
    },
    {
      id: 2,
      nombreNegocio: "Comercial Las",
      propietario: "Willi Las",
      email: "comerciallas@gmail.com",
      telefono: "952537822",
      direccion: "Calle Secundaria 456",
      estado: "Inactivo",
    },
    {
      id: 3,
      nombreNegocio: "Panaderia Jimenez",
      propietario: "Juan Jimenez",
      email: "panaderiajimenez@gmail.com",
      telefono: "979129737",
      direccion: "Pasaje Tercero 789",
      estado: "Activo",
    },
  ]);

  // Función para editar un negocio
  const editarNegocio = (id, nuevosDatos) => {
    const negociosActualizados = negocios.map((negocio) =>
      negocio.id === id ? { ...negocio, ...nuevosDatos } : negocio
    );
    setNegocios(negociosActualizados);
  };

  // Función para eliminar un negocio
  const eliminarNegocio = (id) => {
    const negociosRestantes = negocios.filter((negocio) => negocio.id !== id);
    setNegocios(negociosRestantes);
  };

  return (
    <div className="negocio-table-container">
      <h2>Lista de Negocios</h2>
      <table className="negocio-table">
        <thead>
          <tr>
            <th>ID</th>
            <th>Nombre Negocio</th>
            <th>Propietario</th>
            <th>Email</th>
            <th>Teléfono</th>
            <th>Dirección</th>
            <th>Estado</th>
            <th>Confirmar Edicion</th>
            <th>Confirmar Elimarnacion</th>
          </tr>
        </thead>
        <tbody>
          {negocios.map((negocio) => (
            <tr key={negocio.id}>
              <td>{negocio.id}</td>
              <td>{negocio.nombreNegocio}</td>
              <td>{negocio.propietario}</td>
              <td>{negocio.email}</td>
              <td>{negocio.telefono}</td>
              <td>{negocio.direccion}</td>
              <td>{negocio.estado}</td>
              <td>
                <button
                  className="btn-editar"
                  onClick={() =>
                    editarNegocio(negocio.id, {
                      nombreNegocio: prompt("Nuevo nombre del negocio", negocio.nombreNegocio),
                      telefono: prompt("Nuevo teléfono", negocio.telefono),
                    })
                  }
                >
                  Editar
                </button>
              </td>
              <td>
              <button
                  className="btn-eliminar"
                  onClick={() => eliminarNegocio(negocio.id)}
                >
                  Eliminar
                </button>

              </td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
};

export default EditarNegocio;