import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import axios from "axios"; 
import './MenuFunctions.css';
import Resumen from './Resumen.js';
import AgregarNegocio from './AgregarNegocio.js';
import EditarNegocio from './EditarNegocio.js';
import EliminarNegocio from './EliminarNegocio.js';
import AgregarPlato from './AgregarPlato.js';
import EditarPlato from './EditarPlato.js';
import EliminarPlato from './EliminarPlato.js';

const MenuFunctions = () => {
  const [activeComponent, setActiveComponent] = useState(""); 
  const navigate = useNavigate();

  const loadComponent = (component) => {
    setActiveComponent(component);
  };

  const renderComponent = () => {
    switch (activeComponent) {
      case "resumen":
        return <Resumen />;
      case "agregarnegocio":
        return <AgregarNegocio />;
      case "editarnegocio":
        return <EditarNegocio />;
      case "eliminarnegocio":
        return <EliminarNegocio />;
      case "agregarplato":
        return <AgregarPlato />;
      case "editarplato":
        return <EditarPlato />;
      case "eliminarplato":
        return <EliminarPlato />;
      default:
        return (
          <div className="dashboard">
            <h2>¡Bienvenido al Panel de Funciones!</h2>
            <p>Selecciona una opción del menú para empezar.</p>
          </div>
        );
    }
  };

  const handleLogout = async () => {
    try {
      const response = await axios.post("https://api-degusto.onrender.com/api/user/logout");
      if (response.status === 200) {
        console.log('Sesión cerrada');
        navigate("/loginregister");
      } else {
        console.error("Error al cerrar sesión:", response.data);
        alert("Hubo un problema al cerrar sesión.");
      }
    } catch (error) {
      console.error("Error de red al cerrar sesión:", error);
      alert("Error de red. Por favor, intenta nuevamente.");
    }
  };

  return (
    <div className="owner-container">
      <nav className="owner-menu">
        <h3 className="owner-title">Panel de Funciones</h3>
        <ul className="owner-list">
          {[
            { key: "resumen", label: "Resumen" },
            { key: "agregarnegocio", label: "Agregar Negocio" },
            { key: "editarnegocio", label: "Editar Negocio" },
            { key: "eliminarnegocio", label: "Eliminar Negocio" },
            { key: "agregarplato", label: "Agregar Plato" },
            { key: "editarplato", label: "Editar Plato" },
            { key: "eliminarplato", label: "Eliminar Plato" }
          ].map(({ key, label }) => (
            <li
              key={key}
              className={activeComponent === key ? "active" : ""}
              onClick={() => loadComponent(key)}
            >
              {label}
            </li>
          ))}
        </ul>
      </nav>
      <div className="owner-main">
        <header className="owner-header">
          <span>Bienvenido, Administrador</span>
          <button className="btn-logout" onClick={handleLogout}>Cerrar Sesión</button>
        </header>
        <div className="content">{renderComponent()}</div>
      </div>
    </div>
  );
};

export default MenuFunctions;