import React, { useState } from "react";
import "./VistaNegocio.css";

const VistaNegocio = () => {
  // Datos de prueba
  const [negocios, setNegocios] = useState([
    {
      id: 1,
      nombre: "Cafetería El Buen Café",
      categoria: "Cafetería",
      direccion: "Av. Siempre Viva 123",
      imagen: "https://via.placeholder.com/150",
      validado: false,
    },
    {
      id: 2,
      nombre: "Restaurante La Parrilla",
      categoria: "Restaurante",
      direccion: "Calle Falsa 456",
      imagen: "https://via.placeholder.com/150",
      validado: true,
    },
    {
      id: 3,
      nombre: "Panadería El Trigo",
      categoria: "Panadería",
      direccion: "Plaza Central 789",
      imagen: "https://via.placeholder.com/150",
      validado: false,
    },
  ]);

  // Función para validar un negocio
  const validarNegocio = (id) => {
    const nuevosNegocios = negocios.map((negocio) =>
      negocio.id === id ? { ...negocio, validado: true } : negocio
    );
    setNegocios(nuevosNegocios);
  };

  return (
    <div className="vista-negocio-container">
      <h2>Monitoreo de Negocios</h2>
      <table className="negocios-table">
        <thead>
          <tr>
            <th>Nombre</th>
            <th>Categoría</th>
            <th>Dirección</th>
            <th>Imagen</th>
            <th>Estado</th>
            <th>Acciones</th>
          </tr>
        </thead>
        <tbody>
          {negocios.map((negocio) => (
            <tr key={negocio.id}>
              <td>{negocio.nombre}</td>
              <td>{negocio.categoria}</td>
              <td>{negocio.direccion}</td>
              <td>
                <img
                  src={negocio.imagen}
                  alt={negocio.nombre}
                  className="negocio-imagen"
                />
              </td>
              <td>
                {negocio.validado ? (
                  <span className="estado-validado">Validado</span>
                ) : (
                  <span className="estado-pendiente">Pendiente</span>
                )}
              </td>
              <td>
                {!negocio.validado && (
                  <button
                    className="btn-validar"
                    onClick={() => validarNegocio(negocio.id)}
                  >
                    Validar
                  </button>
                )}
              </td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
};

export default VistaNegocio;