import React, { useEffect, useState } from "react";
import axios from "axios";
import "./VistaUsuario.css";

const VistaUsuario = () => {
  const [users, setUsers] = useState([]);
  const [loading, setLoading] = useState(true); // Indicador de carga
  const [error, setError] = useState(null); // Manejo de errores

  // Función para obtener los usuarios desde la API
  const fetchUsers = async () => {
    try {
      const response = await axios.get("https://api-degusto.onrender.com/api/user", {
        //withCredentials: true, // Agregar con la opción withCredentials
      });
      // Filtrar solo los usuarios con personTypeName "Owner" o "Customer"
      const filteredUsers = response.data.filter(
        (user) => user.personTypeName === "Owner" || user.personTypeName === "Customer"
      );
      setUsers(filteredUsers);
    } catch (err) {
      console.error("Error al obtener los usuarios:", err);
      setError("No se pudieron cargar los usuarios.");
    } finally {
      setLoading(false);
    }
  };

  // Efecto para cargar los usuarios al montar el componente
  useEffect(() => {
    fetchUsers();
  }, []);

  // Función para validar o denegar un usuario
  const updateUserStatus = async (userId, newStatus, personTypeId) => {
    try {
      await axios.put(`https://api-degusto.onrender.com/api/user/${userId}`, {
        personTypeId,
        userStatus: newStatus,
      }, {
        //withCredentials: true, // Agregar con la opción withCredentials
      });
      // Actualizar el estado local después de la acción
      setUsers((prevUsers) =>
        prevUsers.map((user) =>
          user.userId === userId ? { ...user, userStatus: newStatus } : user
        )
      );
    } catch (err) {
      console.error("Error al actualizar el estado del usuario:", err);
    }
  };

  // Función para eliminar un usuario
  const deleteUser = async (userId) => {
    try {
      const response = await axios.delete(`https://api-degusto.onrender.com/api/user/${userId}`, {
        //withCredentials: true, // Agregar con la opción withCredentials
      });
      if (response.data === true) {
        // Eliminar el usuario del estado local
        setUsers((prevUsers) => prevUsers.filter((user) => user.userId !== userId));
      }
    } catch (err) {
      console.error("Error al eliminar el usuario:", err);
    }
  };

  // Mostrar indicador de carga
  if (loading) return <div>Cargando usuarios...</div>;
  if (error) return <div>{error}</div>;

  return (
    <div className="user-table-container">
      <h2>Lista de Usuarios</h2>
      <table className="user-table">
        <thead>
          <tr>
            <th>Nro</th>
            <th>Usuario</th>
            <th>Nombre</th>
            <th>Email</th>
            <th>Teléfono</th>
            <th>Tipo</th>
            <th>Fecha de creación</th>
            <th>Estado</th>
            <th>Acciones</th>
          </tr>
        </thead>
        <tbody>
          {users.map((user, index) => (
            <tr key={user.userId}>
              <td>{index + 1}</td>
              <td>{user.userName}</td>
              <td>{user.PersonName}</td>
              <td>{user.personEmail}</td>
              <td>{user.personPhoneNumber}</td>
              <td>{user.personTypeName === "Owner" ? "Dueño" : "Cliente"}</td>
              <td>{new Date(user.userCreatedAt).toLocaleString()}</td>
              <td>
                <span
                  className={`estado-${user.userStatus === 1 ? "validado" : "pendiente"}`}
                >
                  {user.userStatus === 1 ? "Validado" : "Pendiente"}
                </span>
              </td>
              <td>
                <button
                  className="btn-validar"
                  onClick={() => updateUserStatus(user.userId, 1, user.personTypeName === "Owner" ? 1 : 2)}
                >
                  Validar
                </button>
                <button
                  className="btn-denegar"
                  onClick={() => updateUserStatus(user.userId, 0, user.personTypeName === "Owner" ? 1 : 2)}
                >
                  Denegar
                </button>
                <button
                  className="btn-eliminar"
                  onClick={() => deleteUser(user.userId)}
                >
                  Eliminar
                </button>
              </td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
};

export default VistaUsuario;