import React, { useEffect, useState } from 'react';
import axios from 'axios';
//import Cookies from 'js-cookie';
import './LoginRegister.css';
import { useNavigate } from 'react-router-dom';

const LoginRegister = () => {
  const [loginMessage, setLoginMessage] = useState('');
  const [registerMessage, setRegisterMessage] = useState('');
  const [isLoginVisible, setIsLoginVisible] = useState(true);
  const navigate = useNavigate();

  // Maneja parámetros en la URL para mostrar mensajes específicos
  useEffect(() => {
    const params = new URLSearchParams(window.location.search);
    const messageParam = params.get('message');

    if (messageParam === 'login_error') {
      setLoginMessage('Correo o contraseña incorrectos.');
    } else if (messageParam === 'register_success') {
      setLoginMessage('Registro exitoso. Por favor, inicia sesión.');
    }
  }, []);

  // Función para manejar el login
  const handleLogin = async (e) => {
    e.preventDefault();
    const username = e.target.username.value;
    const password = e.target.password.value;

    console.log('Enviando datos:', { username, password });

    try {
      const response = await axios.post(
        'https://api-degusto.onrender.com/api/user/login',
        { userName: username, userPassword: password }
      );

      console.log('Respuesta del servidor:', response.data);

      const { person_TypeId, userName, userStatus, personEmail } = response.data;

      if (userName) {
        if (person_TypeId === 3) {
          // Administrador
          console.log('Bienvenido, administrador');
          navigate('/menuadmin'); // Redirige a la página de administrador
        } else if (person_TypeId === 1) {
          // Dueño de negocio
          if (userStatus === 1) {
            console.log('Bienvenido, dueño de negocio');
            navigate('/menufunctions'); // Redirige a la página de dueño de negocio
          } else if (userStatus === 0) {
            setLoginMessage('Tu cuenta aún no ha sido aprobada. Por favor, espera la confirmación.');
          } else {
            setLoginMessage('Estado de cuenta no reconocido. Contacta con el soporte.');
          }
        } else {
          setLoginMessage('Tipo de usuario no reconocido.');
        }
      } else {
       setLoginMessage(response.data.message || 'Usuario o contraseña incorrectos');
      }
    } catch (error) {
      console.error('Error de login:', error.response?.data?.message || 'Error desconocido');
      setLoginMessage('Error en el inicio de sesión. Por favor, inténtalo de nuevo.');
    }
  };

  // Función para manejar el registro
  const handleRegister = async (e) => {
    e.preventDefault();
    const name = e.target.name.value;
    const email = e.target.email.value;
    const phone = e.target.phone.value;
    const username = e.target.username.value;
    const password = e.target.password.value;

    console.log('Enviando datos:', { name, email, phone, username, password });

    try {
      const response = await axios.post('https://api-degusto.onrender.com/api/user/register', {
        personTypeId: 1, // Asignar "dueño de negocio"
        name: name,
        email: email,
        phoneNumber: phone,
        userName: username,
        password: password,
        createDate: new Date().toISOString(), // Formato DD/MM/YYYY
      });

      console.log('Respuesta del servidor:', response.data);

      setRegisterMessage('Envío exitoso. Espere hasta que se confirme su registro.');
      setIsLoginVisible(true); // Muestra el formulario de login
    } catch (error) {
      console.error('Error en el registro:', error.response?.data?.message || 'Error desconocido');
      setRegisterMessage('Error en el registro. Por favor, revisa los datos.');
    }
  };

  return (
    <main>
      <div className="contenedor__todo">
        <div className="caja__trasera">
          <div className="caja__trasera-login">
            <h3>¿Ya tienes una cuenta?</h3>
            <p>Inicia sesión para entrar en la página</p>
            <button id="btn__iniciar-sesion" onClick={() => setIsLoginVisible(true)}>Iniciar Sesión</button>
          </div>
          <div className="caja__trasera-register">
            <h3>¿Aún no tienes una cuenta?</h3>
            <p>Regístrate para que puedas iniciar sesión</p>
            <button id="btn__registrarse" onClick={() => setIsLoginVisible(false)}>Regístrarse</button>
          </div>
        </div>
        <div className="body::before ">
      </div>
        <div className="contenedor__login-register">
          {isLoginVisible ? (
            <form onSubmit={handleLogin} className="formulario__login">
              <h2>Iniciar Sesión</h2>
              <input type="text" name="username" placeholder="Nombre de Usuario" required />
              <input type="password" name="password" placeholder="Contraseña" required />
              <button type="submit">Entrar</button>
              <div id="login-message">{loginMessage}</div>
            </form>
          ) : (
            <form onSubmit={handleRegister} className="formulario__register">
              <h2>Regístrarse</h2>
              <input type="text" name="name" placeholder="Nombre completo" required />
              <input type="email" name="email" placeholder="Correo Electrónico" required />
              <input type="text" name="phone" placeholder="Número de celular" required />
              <input type="text" name="username" placeholder="Usuario" required />
              <input type="password" name="password" placeholder="Contraseña" required />
              <button type="submit">Regístrarse</button>
              <div id="register-message">{registerMessage}</div>
            </form>
          )}
        </div>
      </div>
    </main>
  );
};

export default LoginRegister;