import React, { useState } from "react";
import { useNavigate } from "react-router-dom"; // Para redirigir al LoginRegister.js
import axios from "axios"; // Importamos Axios
import "./MenuAdmin.css";
import VistaNegocio from "./VistaNegocio";
import VistaUsuario from "./VistaUsuario"; 
import VistaResena from "./VistaResena";

const MenuAdmin = () => {
  const [activeComponent, setActiveComponent] = useState(""); // Controla qué componente se muestra
  const navigate = useNavigate();

  const loadComponent = (component) => {
    setActiveComponent(component);
  };

  const renderComponent = () => {
    switch (activeComponent) {
      case "vistausuario":
        return <VistaUsuario />; // Carga la interfaz de validación de usuarios
      case "vistanegocio":
        return <VistaNegocio />; // Carga la interfaz de monitoreo de negocios
      case "vistaresena":
        return <VistaResena />; // Carga la interfaz de monitoreo de reseñas
      default:
        return (
          <div className="dashboard">
            <h2>¡Bienvenido al Panel de Administración!</h2>
            <p>Selecciona una opción del menú para empezar.</p>
          </div>
        );
    }
  };

  const handleLogout = async () => {
    try {
      // Realizamos la solicitud POST con Axios
      const response = await axios.post("https://api-degusto.onrender.com/api/user/logout");

      if (response.status === 200) {
        // Redirige al componente LoginRegister.js
        console.log('Sesión cerrandose');
        navigate("/loginregister"); // Cambia la ruta según cómo esté definida en tu sistema
      } else {
        console.error("Error al cerrar sesión:", response.data);
        alert("Hubo un problema al cerrar sesión.");
      }
    } catch (error) {
      console.error("Error de red al cerrar sesión:", error);
      alert("Error de red. Por favor, intenta nuevamente.");
    }
  };

  return (
    <div className="admin-container">
      {/* Barra lateral */}
      <nav className="admin-menu">
        <h3 className="menu-title">Panel de Administración</h3>
        <ul className="menu-list">
          <li
            className={activeComponent === "vistausuario" ? "active" : ""}
            onClick={() => loadComponent("vistausuario")}
          >
            Validar Usuarios
          </li>
          <li
            className={activeComponent === "vistanegocio" ? "active" : ""}
            onClick={() => loadComponent("vistanegocio")}
          >
            Monitorear Negocios
          </li>
          <li
            className={activeComponent === "vistaresena" ? "active" : ""}
            onClick={() => loadComponent("vistaresena")}
          >
            Monitorear Reseñas
          </li>
        </ul>
      </nav>

      {/* Contenido */}
      <div className="admin-main">
        <header className="admin-header">
          <span>Bienvenido, Administrador</span>
          <button className="btn-logout" onClick={handleLogout}>Cerrar Sesión</button>
        </header>
        <div className="content">{renderComponent()}</div>
      </div>
    </div>
  );
};

export default MenuAdmin;
