import React, { useState } from "react";
import "./VistaResena.css";

const VistaResena = () => {
  // Datos de prueba para negocios
  const negocios = [
    { id: 1, nombre: "Restaurante Los Sabores", reseñas: [] },
    {
      id: 2,
      nombre: "Cafetería El Aroma",
      reseñas: [
        { id: 1, usuario: "Juan Pérez", contenido: "¡Excelente servicio y café!", fecha: "2024-11-01" },
        { id: 2, usuario: "Ana López", contenido: "El café estaba muy frío, mala experiencia.", fecha: "2024-11-02" },
      ],
    },
    {
      id: 3,
      nombre: "Pizzería La Familia",
      reseñas: [
        { id: 3, usuario: "Carlos Gómez", contenido: "La pizza estaba deliciosa, volveré pronto.", fecha: "2024-11-05" },
      ],
    },
  ];

  // Estado para el negocio seleccionado y las reseñas visibles
  const [negocioSeleccionado, setNegocioSeleccionado] = useState(null);

  // Función para seleccionar un negocio
  const seleccionarNegocio = (negocio) => {
    setNegocioSeleccionado(negocio);
  };

  // Función para eliminar una reseña
  const eliminarReseña = (idReseña) => {
    const actualizado = negocioSeleccionado.reseñas.filter((reseña) => reseña.id !== idReseña);
    setNegocioSeleccionado({ ...negocioSeleccionado, reseñas: actualizado });
  };

  return (
    <div className="vista-resena">
      <h2>Monitoreo de Reseñas</h2>
      <div className="contenedor-negocios">
        <h3>Negocios Disponibles</h3>
        <ul className="lista-negocios">
          {negocios.map((negocio) => (
            <li
              key={negocio.id}
              onClick={() => seleccionarNegocio(negocio)}
              className={negocioSeleccionado?.id === negocio.id ? "negocio-activo" : ""}
            >
              {negocio.nombre}
            </li>
          ))}
        </ul>
      </div>

      <div className="contenedor-reseñas">
        {negocioSeleccionado ? (
          <>
            <h3>Reseñas de {negocioSeleccionado.nombre}</h3>
            {negocioSeleccionado.reseñas.length > 0 ? (
              <table className="tabla-reseñas">
                <thead>
                  <tr>
                    <th>Usuario</th>
                    <th>Reseña</th>
                    <th>Fecha</th>
                    <th>Acciones</th>
                  </tr>
                </thead>
                <tbody>
                  {negocioSeleccionado.reseñas.map((reseña) => (
                    <tr key={reseña.id}>
                      <td>{reseña.usuario}</td>
                      <td>{reseña.contenido}</td>
                      <td>{reseña.fecha}</td>
                      <td>
                        <button onClick={() => eliminarReseña(reseña.id)}>Eliminar</button>
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            ) : (
              <p>No hay reseñas disponibles para este negocio.</p>
            )}
          </>
        ) : (
          <p>Selecciona un negocio para ver sus reseñas.</p>
        )}
      </div>
    </div>
  );
};

export default VistaResena;